.container{
    width: 500px;
    padding: 2px;
    margin: auto;
    background-color: lightblue;
    border-radius: 6px;
    margin-top: 22px;
}
.container button{
    border: none;
    background-color: transparent;
    font-size: 40px;
    margin-left: 11px;
    cursor: pointer;
    
}
span{
    display: flex;
    align-items: center;
    
}
span p{
    font-size: 22px;
    margin-left: 5px;
    position: absolute;
    margin-left:46px;
    
}
.ans{
    margin-left: 50px;
    position: relative;
    top: -25px;
    font-size: 20px;
}
@media screen and (max-width:600px){
    .container{
        width: 90%;
    }
}